import Vue from 'vue'
import Vuex from 'vuex'
import api from '@/api'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    list: [],
    size: 4,
    totalPage: 0,
    nowPage: 1,
    count: 0,
    activeStu: null,
    modalShow: false,
    keywords: ''
  },

  mutations: {
    setList(state, list) {
      state.list = list
    },
    setTotalPage(state, count) {
      state.count = count
      state.totalPage = Math.ceil(count / state.size)
    },
    setActiveStu(state, stu) {
      state.activeStu = stu
    },
    setModalShow(state, bool) {
      state.modalShow = bool
    },
    setNowPage(state, page) {
      state.nowPage = page
    },
    setKeyWords(state, kw) {
      state.keywords = kw
    }
  },

  actions: {
    /**
     * 异步请求获取学生列表数据
     * @param {*} param0 
     * @param {*} page 
     */
    getStuList({ commit, state }) {
      api.findByPage(state.nowPage, state.size).then(data => {
        commit('setList', data.data.data.findByPage)
        commit('setTotalPage', data.data.data.cont)
      })
    },
    /**
     * 修改学生数据后提交新的数据
     * @param {*} param0 
     * @param {*} stu 
     */
    updateStu({ commit, state }, stu) {
      return api.updateStu(stu).then(data => {
        console.log(data)
        if (data.data.status == 'fail') {
          return { msg: data.data.msg, type: 2 }
        } else {
          commit('setModalShow', false)
          return { msg: data.data.msg, type: 1 }
        }
      })
    },
    /**
     * 翻页请求数据
     * @param {*} param0 
     * @param {*} page 
     */
    turnPage({ commit, state, dispatch }, page) {
      if (page == "pre") {
        if (state.nowPage > 1) {
          commit('setNowPage', state.nowPage - 1)
        }
      } else if (page == "next") {
        if (state.nowPage < state.totalPage) {
          commit("setNowPage", state.nowPage + 1)
        }
      } else {
        commit("setNowPage", page)
      }
      if (state.keywords) {
        dispatch('searchStu')
      } else {
        dispatch("getStuList")
      }
    },
    /**
     *删除学生数据 
     * @param {*} param0 
     * @param {*} sNo 
     */
    delStu({ commit, state, dispatch }, sNo) {
      api.delStu(sNo).then(() => {
        if (state.list.length == 1) {
          if (state.nowPage == state.totalPage) {
            dispatch('turnPage', state.nowPage - 1)
          } else {
            dispatch('turnPage', state.nowPage)
          }
        } else {
          dispatch('turnPage', state.nowPage)
        }
      })
    },
    searchStu({ commit, state, dispatch }) {
      api.searchStu({
        search: state.keywords,
        page: state.nowPage,
        size: state.size,
        sex: -1
      }).then(data => {
        commit('setList', data.data.data.searchList)
        commit("setTotalPage", data.data.data.cont)
      })
    }
  },
  modules: {

  }
})
