<template>
  <div class="student-list content" id="student-list">
    <stu-search />
    <stu-table />
    <turn-page />
    <transition>
      <show-modal v-if="flag" />
    </transition>
  </div>
</template>

<script>
import stuSearch from "../components/stuList/stuSearch";
import stuTable from "../components/stuList/stuTable";
import turnPage from "../components/stuList/turnPage";
import showModal from "@/components/stuList/showModal";
export default {
  components: {
    stuSearch,
    stuTable,
    turnPage,
    showModal
  },
  computed: {
    flag() {
      return this.$store.state.modalShow;
    }
  }
};
</script>

<style>
/* 刚进入 */
.v-enter {
  top: -100%;
  opacity: 0;
}
/* 完全进入 */
.v-enter-to {
  top: 0;
  opacity: 1;
}

.v-leave {
  top: 0;
  opacity: 1;
}

.v-leave-to {
  top: -100%;
  opacity: 0;
}

.v-enter-active {
  transition: all 0.5s;
}

.v-leave-active {
  transition: all 0.5s;
}
</style>