import Vue from "vue"
import toast from './index.vue'

const Toast = Vue.extend(toast)

export default function ({ msg, type } = { msg: 'OK', type: 1 }, duration = 2000) {
    let app = new Toast({
        el: document.createElement('div'),
        data: {
            msg,
            type,
            flag: true,
            show: true
        },
        computed: {
            typs() {
                switch (this.type) {
                    case 1:
                        return "√";
                    case 2:
                        return "×"
                    default:
                        return "×";
                }
            },
            cls() {
                switch (this.type) {
                    case 1:
                        return "suc";
                    case 2:
                        return "err";
                    default:
                        return "err";
                }
            }
        }
        

    })
    document.body.appendChild(app.$el)
    setTimeout(function () {
        app.flag = false
      },duration-200)
    setTimeout(function () {
     app.show = false
   },duration)
}