import Axios from "axios"
import URLs from "./URLs"
const Request = Axios.create({
    baseURL: URLs.base,
    method: "GET",
    params: {
        appkey: 'Towe_1582352374649'
    }
})
function findByPage(page, size) {
    return Request({
        url: URLs.findByPage,
        params: {
            page,
            size
        }
    })
}
function updateStu(options) {
    return Request({
        url: URLs.updateStu,
        params: {
            ...options
        }
    })
}
function addStu(options) {
    return Request({
        url: URLs.addStu,
        params: {
            ...options
        }
    })
  }
function delStu(sNo) {
    return Request({
        url: URLs.delStu,
        params: {
            sNo
        }
    })
}
function searchStu(options) {
    return Request({
        url:URLs.searchStu,
        params:{
            ...options
        }
    })
}
export default {
    findByPage,
    updateStu,
    addStu,
    delStu,
    searchStu
} 