<template>
  <div id="toast" :class="[flag?'fadeIn':'fadeOut']" v-if="show">
    <div id="content" :class="cls">{{typs}}{{msg}}</div>
  </div>
</template>

<script>
export default {};
</script>

<style>
#toast {
  width: 200px;
  height: 50px;
  background: lightblue;
  color: white;
  text-align: center;
  line-height: 50px;
  position: fixed;
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
}
@keyframes fadeIn {
  0% {
    top: 0;
    opacity: 0;
  }
  100% {
    top: 40px;
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    top: 40px;
    opacity: 1;
  }
  100% {
    top: 0;
    opacity: 0;
  }
}

.fadeIn {
  animation: fadeIn 0.5s forwards;
}

.fadeOut {
  animation: fadeOut 0.5s forwards;
}

#content {
  width: 100%;
}

.err {
  background-color: red;
  color: pink;
}

.suc {
  background-color: aquamarine;
  color: white;
}
</style>